.Welcome {
    color: white;
    text-align: center;
    height: 100vh;
    width: 100vw;
    font-size: calc(10px + 2vmin);
    position: relative;
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95), rgba(128, 128, 128, 0.8)), url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
}

#Logo {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url('./peach.gif');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    height: 25vh;
    width: 25vh;
}

.English-select {
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95), rgba(128, 128, 128, 0.8)), url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
    clip-path: polygon(0 0, 30% 0, 30% 100%, 0 100%);
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
    transition: all 1.5s;
}

.English-select::after {
    content: 'Start';
    position: fixed;
    font-size: 1.5em;
    left: 4.5%;
    bottom: 7.7%;
    z-index: 3;
    color: whitesmoke;
}

.English-select:hover {
    cursor: pointer;
    background-image: linear-gradient(to top, #0d324df5, #7f5a83f5), url('./Ludo.jpg');
    clip-path: polygon(0 0, 70% 0, 70% 100%, 0 100%);
    background-size: cover;
    background-position: left;
    z-index: 3;
}

.English-select:hover::before {
    content: "Spanking Ludo is an easy-to-use spanking game for spanko partners.";
    font-size: 1.7vw;
    color: white;
    position: absolute;
    left: 4.5%;
    top: 12%;
}

.English-select:hover::after {
    content: "English";
    font-size: 5vw;
    color: white;
    position: absolute;
    left: 4.5%;
    bottom: 7.7%;
}

.Chinese-select {
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95), rgba(128, 128, 128, 0.8)), url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
    position: absolute;
    clip-path: polygon(70% 0, 100% 0, 100% 100%, 70% 100%);
    height: 100%;
    width: 100%;
    transition: all 1.5s;
    z-index: 2;
}

.Chinese-select::after {
    content: '开始';
    position: fixed;
    font-size: 1.5em;
    right: 4.5%;
    bottom: 7.7%;
    z-index: 3;
    color: whitesmoke;
}

.Chinese-select:hover {
    cursor: pointer;
    background-image: linear-gradient(to top, #0d324df5, #7f5a83f5), url('./Ludo.jpg');
    background-size: cover;
    clip-path: polygon(30% 0, 100% 0, 100% 100%, 30% 100%);
    background-position: right;
    z-index: 3;
}

.Chinese-select:hover::before {
    content: "欢迎来到SP飞行棋游戏。";
    font-size: 4vw;
    color: white;
    position: absolute;
    right: 7%;
    top: 12%;
}

.Chinese-select:hover::after {
    content: "简体中文";
    font-size: 5vw;
    color: white;
    position: absolute;
    right: 4.5%;
    bottom: 7.7%;
}

#English-start {
    position: absolute;
    bottom: 100px;
    left: 100px;
    color: whitesmoke;
    z-index: 3;
}

#Chinese-start {
    position: absolute;
    bottom: 100px;
    right: 100px;
    color: whitesmoke;
    z-index: 3;
}


@media screen and (orientation: portrait) {
    .Chinese-select:hover::before {
        content: "";
    }

    .English-select:hover::before {
        content: "";
    }

}