.Cell {
    word-wrap: break-word;
    width: 10%;
    height: 12.5%;
    background-color: rgb(211, 211, 181);
    border: 1px solid black;
    color: black;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    transition: all 2s;
    overflow: hidden;

    display: flex;
    justify-content:center;
    align-items:center;
}

.Cell-current {
    word-wrap: break-word;
    width: 10%;
    height: 12.5%;
    background-color: indigo;
    border: 1px solid black;
    color: gold;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    overflow: hidden;
    
    display: flex;
    justify-content:center;
    align-items:center;
}

