.group-select-off{
    display: none;
}

.group-select-container{
    background-color: #16242e;
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 2;
}

#group-prompt{
    position:absolute;
    top: 8%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.group-photo-container{
    position:relative;
    transform: translate(-50%,-50%);
    top:50%;
    left:50%;
    height: 67.5vmin;
    width: 90vmin;
}

#hbar{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 15%;
}

#vbar{
    position: absolute;
    top: 15%;
    left: 0;
    height: 75%;
    width: 25%;
}

#g1{
    position: absolute;
    top: 15%;
    left: 25%;
    height: 25%;
    width: 25%;
}

#g1:hover{
    cursor: pointer;
