  #cube .front {
    transform:
      translateZ(100px);
  }

  #cube .back {
    transform:
      rotateX(-180deg) translateZ(100px);
  }

  #cube .right {
    transform:
      rotateY(90deg) translateZ(100px);
  }

  #cube .left {
    transform:
      rotateY(-90deg) translateZ(100px);
  }

  #cube .top {
    transform:
      rotateX(90deg) translateZ(100px);
  }

  #cube .bottom {
    transform:
      rotateX(-90deg) translateZ(100px);
  }

  .die-container {
    width: 200px;
    height: 200px;
    position: absolute;
    top: 5vh;
    left: 0;
    margin: 0 auto 40px;

    perspective: 1000px;
    perspective-origin: 50% 100%;
  }

  #cube {
    width: 100%;
    height: 100%;
    top: 100px;
    position: absolute;

    transform-style: preserve-3d;

    transition: transform 4s;
  }

  #cube:hover {
    cursor: pointer;
  }

  #cube div {
    background: hsla(227, 77%, 22%, 0.95);
    display: block;
    position: absolute;
    width: 200px;
    height: 100px;
    border: 2px solid #8fb2d3;
    border-radius: 5px;

    margin: 0 auto;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 500%;
    text-align: center;
    padding: 50px 0;
  }

  .dot {
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    background: #fff;
    border-radius: 15px;
  }

  .front .dot1 {
    top: 85px;
    left: 85px;
  }

  .back .dot1 {
    top: 45px;
    left: 45px;
  }

  .back .dot2 {
    top: 125px;
    left: 125px;
  }

  .right .dot1 {
    top: 45px;
    left: 45px;
  }

  .right .dot2 {
    top: 85px;
    left: 85px;
  }

  .right .dot3 {
    top: 125px;
    left: 125px;
  }

  .left .dot1 {
    top: 45px;
    left: 45px;
  }

  .left .dot2 {
    top: 45px;
    left: 125px;
  }

  .left .dot3 {
    top: 125px;
    left: 45px;
  }

  .left .dot4 {
    top: 125px;
    left: 125px;
  }

  .top .dot1 {
    top: 45px;
    left: 45px;
  }

  .top .dot2 {
    top: 45px;
    left: 125px;
  }

  .top .dot3 {
    top: 85px;
    left: 85px;
  }

  .top .dot4 {
    top: 125px;
    left: 45px;
  }

  .top .dot5 {
    top: 125px;
    left: 125px;
  }

  .bottom .dot1 {
    top: 45px;
    left: 45px;
  }

  .bottom .dot2 {
    top: 45px;
    left: 85px;
  }

  .bottom .dot3 {
    top: 45px;
    left: 125px;
  }

  .bottom .dot4 {
    top: 125px;
    left: 45px;
  }

  .bottom .dot5 {
    top: 125px;
    left: 85px;
  }

  .bottom .dot6 {
    top: 125px;
    left: 125px;
  }

  .button {
    height: 100px;
    width: 200px;
    background-color: #fff;
  }

  .button:hover {
    cursor: pointer;
  }

  #touch-screen-roll{
    width: 0;
    height: 0;
  }

  @media screen and (orientation: portrait) {

    .die-container {
      width: 200px;
      height: 200px;
      position: absolute;
      top: 30%;
      left: 50%;
      transform: translate(-50%,-50%);
      margin: 0 auto 40px;
  
      perspective: 1000px;
      perspective-origin: 50% 100%;
    }


    #die-container-off {
      opacity: 0;
    }
    
    #die-container-on {
      opacity: 1;
    }
  }