.Board {
    width: 100vmin;
    height: 66.667vmin;
    height: calc(200vmin / 3);
    font-size: 1.2vmin;
    text-align: left;
    position: relative;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
}

@media screen and (orientation: portrait) {
    .Board {
        width: 99vmin;
        height: 75vmin;
        font-size: 1.2vmin;
        text-align: left;
        position: relative;
        top: 0.5%;
        left: 0.5%;
        transform: translate(0,0);
    }
}