.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  font-size: 2vmin;
  color: white;
  position: relative;
}

header{
  height:100vh;
}

footer{
  color: black;
}

/* Force mobile divice's oritention to portrait mode*/

#require-vertical {
  width: 0;
  height: 0;
}

@media screen and  (orientation: landscape) and (max-width: 950px){

  #require-vertical {
    background-color: rgb(90, 41, 125);
    background-image: url(./vertical.gif);
    background-size:30%;
    background-position:center;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 99;
  }
}

/* mobile adaption for rolling the die */

#roll-die{
  width: 0;
  height: 0;
}

@media screen and  (orientation: portrait){

  #roll-die{
    width: 100%;
    height: 100%;
    position:absolute;
    top:0;
    left:0;
  }
}
